/* Item text */

.textContentItem {
	font-size: 14px;
	word-break: break-all;
}

/* Item image */

.imgItemRight {
	object-fit: cover;
	width: 300px;
	height: 300px;
	border-top-left-radius: 8px;
	border-top-right-radius: 8px;
	border-bottom-left-radius: 8px;
	border-bottom-right-radius: 0px;
}

