	border-bottom-right-radius: 8px;
}

.textTimeLeft {
	color: #808888;
	font-size: 12px;
	font-style: italic;
	margin-left: 50px;
}

/* Stickers */

.viewStickers {
	display: flex;
	border-top: 1px solid #e8e8e8;
	height: 100px;
	align-items: center;
	justify-content: space-around;
}

.imgSticker {
	width: 80px;
	height: 80px;
	object-fit: contain;
}

/* Say hi */

.viewWrapSayHi {
	margin-top: 20px;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

.textSayHi {
	color: #808888;
	font-size: 14px;
}

.imgWaveHand {
	width: 40px;
	height: 40px;
	margin-left: 10px;
}
