/* ==========================================================================
General Keyframes animations
========================================================================== */

.animated {
	animation-duration: 0.5s;
	animation-fill-mode: both;
	-webkit-animation-duration: 0.5s;
	-webkit-animation-fill-mode: both;
}

//Delays
.delay-1 {
	animation-delay: 0.25s;
}

.delay-2 {
	animation-delay: 0.5s;
}

.delay-3 {
	animation-delay: 0.75s;
}

.delay-4 {
	animation-delay: 1s;
}

// FADE IN LEFT
@keyframes fadeInLeft {
	from {
		-webkit-transform: translate3d(20px, 0, 0);
		transform: translate3d(20px, 0, 0);
		opacity: 0;
	}

	to {
		-webkit-transform: translate3d(0, 0, 0);
		transform: translate3d(0, 0, 0);
		opacity: 1;
	}
}

@-webkit-keyframes fadeInLeft {
	from {
		-webkit-transform: translate3d(20px, 0, 0);
		transform: translate3d(20px, 0, 0);
		opacity: 0;
	}

	to {
		-webkit-transform: translate3d(0, 0, 0);
		transform: translate3d(0, 0, 0);
		opacity: 1;
	}
}

.preFadeInLeft {
	opacity: 0;
}

.fadeInLeft {
	opacity: 0;
	animation-name: fadeInLeft;
	-webkit-animation-name: fadeInLeft;
}

// FADE IN UP
@keyframes fadeInUp {
	from {
		-webkit-transform: translate3d(0, 20px, 0);
		transform: translate3d(0, 20px, 0);
	}

	to {
		-webkit-transform: translate3d(0, 0, 0);
		transform: translate3d(0, 0, 0);
		opacity: 1;
	}
}

@-webkit-keyframes fadeInUp {
	from {
		-webkit-transform: translate3d(0, 20px, 0);
		transform: translate3d(0, 20px, 0);
	}

	to {
		-webkit-transform: translate3d(0, 0, 0);
		transform: translate3d(0, 0, 0);
		opacity: 1;
	}
}

.preFadeInUp {
	opacity: 0;
}

.fadeInUp {
	opacity: 0;
	animation-name: fadeInUp;
	-webkit-animation-name: fadeInUp;
}

//Gelatine
.gelatine {
	animation: gelatine 0.6s;
	animation-duration: 0.6s;
	-webkit-animation-duration: 0.5s;
	animation-fill-mode: both;
	-webkit-animation-fill-mode: both;
}

@keyframes gelatine {
	from,
	to {
		transform: scale(1, 1);
	}

	25% {
		transform: scale(0.9, 1.1);
	}

	50% {
		transform: scale(1.1, 0.9);
	}

	75% {
		transform: scale(0.95, 1.05);
	}
}

/* ==========================================================================
Classes to change the feel of bulma buttons
========================================================================== */

// CTA buttons

.button {
	cursor: pointer;
	transition: all 0.5s;

	&.cta {
		font-family: 'Open Sans', sans-serif;
		font-size: 1rem;
		font-weight: 600;
		padding: 26px 40px 26px 40px;
	}

	&.is-clear {
		line-height: 0 !important;
	}

	&.rounded {
		border-radius: 500px;
	}

	&.raised:hover {
		box-shadow: 0 14px 26px -12px rgba(0, 0, 0, 0.42),
			0 4px 23px 0px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2) !important;
		opacity: 0.8;
	}

	&.btn-outlined {
		background: transparent;
	}

	&.signup-button {
		font-size: 0.9rem;
		font-weight: 600;
		font-family: 'Open Sans', sans-serif;
		padding: 24px 26px;
		width: 130px;
	}
}

.button {
	&.primary-btn {
		outline: none;
		border-color: $primary;
		background-color: $primary;
		color: $white;
		transition: all 0.5s;

		&:hover {
			color: $white;
		}

		&.raised:hover {
			box-shadow: 0 14px 26px -12px rgba(79, 193, 234, 0.42),
				0 4px 23px 0px rgba(0, 0, 0, 0.12),
				0 8px 10px -5px rgba(79, 193, 234, 0.2) !important;
			opacity: 0.8;
		}

		&.btn-outlined {
			border-color: $primary;
			color: $primary;
			background-color: transparent;

			&:hover {
				color: $white;
				background-color: $primary;
			}
		}
	}

	&.secondary-btn {
		outline: none;
		border-color: $secondary;
		background-color: $secondary;
		color: $white;
		transition: all 0.5s;

		&:hover {
			color: $white;
		}

		&.raised:hover {
			box-shadow: 0 14px 26px -12px rgba(243, 146, 0, 0.42),
				0 4px 23px 0px rgba(0, 0, 0, 0.12),
				0 8px 10px -5px rgba(243, 146, 0, 0.2) !important;
			opacity: 0.8;
		}

		&.btn-outlined {
			border-color: $secondary;
			color: $secondary;
			background-color: transparent;

			&:hover {
				color: $white;
				background-color: $secondary;
			}
		}
	}

	&.button.accent-btn {
		outline: none;
		border-color: $accent;
		background-color: $accent;
		color: $white;
		transition: all 0.5s;

		&:hover {
			color: $white;
		}

		&.raised:hover {
			box-shadow: 0 14px 26px -12px rgba(104, 187, 136, 0.42),
				0 4px 23px 0px rgba(0, 0, 0, 0.12),
				0 8px 10px -5px rgba(104, 187, 136, 0.2) !important;
			opacity: 0.8;
		}

		&.btn-outlined {
			border-color: $accent;
			color: $accent;
			background-color: transparent;

			&:hover {
				color: $white;
				background-color: $accent;
			}
		}
	}
}

