}

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: landscape) {
	.navbar-menu {
		.navbar-end {
			a {
				display: flex;
				justify-content: center;
				align-items: center;
			}

			.navbar-link {
				padding-right: 0 !important;
			}

			.button {
				min-width: 180px;
			}
		}
	}

	.navbar-item {
		&.is-hidden-mobile {
			display: none !important;
		}

		&.is-hidden-desktop.is-hidden-tablet {
			display: flex !important;
		}
	}

	.pushed-image {
		margin-top: 0 !important;
	}
}

/* ==========================================================================
Section Styles
========================================================================== */

//Sections
.section {
	&.section-light-grey {
		background-color: $light-grey;
	}

	&.section-feature-grey {
		background-color: $section-grey;
	}

	&.section-secondary {
		background-color: $secondary;
	}

	&.section-half {
		height: 75vh !important;
	}

	.title,
	.subtitle {
		font-family: 'Open Sans', sans-serif;
	}

	.subtitle {
		&.is-muted {
			color: $muted-grey;
		}
	}
}

//Titles
.title-wrapper {
	max-width: 500px;
	margin: 0 auto;

	.title,
	.subtitle {
		font-family: 'Open Sans', sans-serif;
	}

	.subtitle {
		&.is-muted {
			color: $muted-grey;
		}
	}
}

//Divider
.divider {
	height: 3px;
	border-radius: 50px;
	background: $secondary;
	width: 60px;

	&.is-centered {
		margin: 0 auto;
	}
}

//Wrapper
.content-wrapper {
	padding: 60px 0;
}

//Pulled image
img.pushed-image {
	margin-top: -29vh;
}

//Icon box
.media.icon-box {
	border-top: none !important;

	.media-content .content p {
		span {
			display: block;
		}

		.icon-box-title {
			color: $blue-grey;
			font-size: 1.2rem;
			font-weight: 600;
		}

		.icon-box-text {
			color: $title-grey;
			font-size: 1rem;
			font-weight: 400;
		}
	}
}

/* ==========================================================================
Sidebar Styles
========================================================================== */

//Sidebar animated icon trigger
.menu-icon-wrapper {
	position: relative;
	left: 0;
	top: 0;
	//margin: -12px 0px 0px -12px;
	width: 34px;
	height: 34px;
	pointer-events: none;
	transition: 0.1s;

	svg {
		position: absolute;
		top: -18px;
		left: -18px;
		transform: scale(0.07);
		transform-origin: 0 0;

		path {
			stroke: $secondary;
			stroke-width: 40px;
			stroke-linecap: round;
			stroke-linejoin: round;
			fill: transparent;
			transition: stroke-dasharray 0.5s;

			&.path1 {
				stroke-dashoffset: 5803.15px;
				stroke-dasharray: 2901.57px, 2981.57px, 240px;
			}

			&.path2 {
				stroke-dashoffset: 800px;
				stroke-dasharray: 400px, 480px, 240px;
			}

			&.path3 {
				stroke-dashoffset: 6993.11px;
				stroke-dasharray: 3496.56px, 3576.56px, 240px;
			}
		}
	}

	&.open {
		svg {
			path {
				&.path1 {
					stroke-dasharray: 2901.57px, 5258.15px, 240px;
				}

				&.path2 {
					stroke-dasharray: 400px, 600px, 0px;
				}

				&.path3 {
					stroke-dasharray: 3496.56px, 6448.11px, 240px;
				}
			}
		}
	}

	.menu-icon-trigger {
		position: relative;
		width: 100%;
		height: 100%;
		cursor: pointer;
		pointer-events: auto;
		background: none;
		border: none;
		margin: 0;
		padding: 0;
	}
}

//Sidebar
.sidebar {
	background: $dark-grey;
	width: 280px;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	transform: translateX(-281px);
	transition: all 0.3s;
	z-index: 10000;
	overflow-y: auto;

	&.is-active {
		transform: translateX(0);
	}

	.sidebar-header {
		height: 4.25rem;
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-bottom: 1px solid lighten($dark-grey, 5%);
		padding: 0 20px;

		img {
			height: 32px;
		}

		a {
			width: 24px;
			height: 24px;
		}

		svg {
			stroke: $white;
			transform: rotate(0);
			transition: all 0.3s;
			cursor: pointer;

			&:hover {
				stroke: $secondary;
				transform: rotate(180deg);
			}
		}

		.sidebar-title {
			color: white;
			font-weight: bold;
			font-size: 26px;
		}
	}

	.inner {
		position: relative;

		.sidebar-menu {
			margin: 0;
			padding: 0;
			max-width: 400px;
			list-style: none;
			list-style-type: none;
			font-family: 'Open Sans', sans-serif !important;

			li {
				a {
					padding: 20px 25px;
					display: block;
					text-decoration: none;
					color: $white;

					&:hover {
						padding: 20px 25px;
						display: block;
						text-decoration: none;
						color: $white;
					}
				}

				a span {
					margin-right: 20px;
					color: $white;
				}

				&.have-children {
					ul {
						padding: 0px;
					}

					li {
						a {
							background-color: darken($dark-grey, 5%);
							padding-left: 62px;
							border-bottom: 1px solid darken($dark-grey, 2%);
							font-size: 0.8rem;

							&:hover {
								color: $primary;
								padding-left: 62px;
							}
						}
					}

					span::after {
						position: absolute;
						top: 27px;
						right: 30px;
						content: '\f054';
						color: $white;
						transition: all 0.5s;
						font-weight: 200 !important;
						font-size: 0.8rem;
					}
				}
			}
		}

		li.have-children,
		li {
			position: relative;
		}

		li.have-children.active > a,
		li.have-children.active > a span,
		li.have-children.active > a span:after {
			color: $secondary;
		}

		li.active.have-children span::after {
			-moz-transform: rotate(90deg);
			-o-transform: rotate(90deg);
			-webkit-transform: rotate(90deg);
			transform: rotate(90deg);
		}

		.sidebar-menu .have-children > ul {
			display: none;
		}
	}
}

/* ==========================================================================
Testimonials Styles
========================================================================== */

.testimonial {
	position: relative;
	overflow: hidden;
	margin: 10px auto;
	min-width: 220px;
	max-width: 310px;
	width: 100%;
	color: #333;
	text-align: left;
	box-shadow: none !important;

	* {
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
	}

	img {
		max-width: 100%;
		height: 80px;
		width: 80px;
		border-radius: 50%;
		margin-right: 5px;
		display: block;
		z-index: 1;
		position: absolute;
		right: 60%;
	}

	blockquote {
		margin: 0;
		display: block;
		border-radius: 8px;
		position: relative;
		background-color: $smoke-white;
		padding: 30px 50px 65px 50px;
		font-size: 1.2rem;
		font-weight: 500;
		margin: 0 0 -40px;
		line-height: 1.6em;
		box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
	}

	blockquote:before,
	blockquote:after {
		font-family: 'FontAwesome';
		content: '\f10d';
		position: absolute;
		font-size: 20px;
		opacity: 0.3;
		font-style: normal;
	}

	blockquote:before {
		top: 35px;
		left: 20px;
	}

	blockquote:after {
		content: '\f10e';
		right: 20px;
		bottom: 35px;
	}

	.author {
		margin: 0;
		height: 80px;
		display: block;
		text-align: left;
		color: $white;
		padding: 0 35px;
		position: relative;
		z-index: 0;

		h5,
		span {
			left: 50%;
			position: absolute;
			opacity: 0.8;
			padding: 3px 5px;
		}

		h5 {
			text-transform: capitalize;
			bottom: 60%;
			margin: 0;
			font-weight: 600;
			font-size: 1.2rem;
			color: $blue-grey;
		}

		span {
			font-size: 0.8em;
			color: $white;
			top: 50%;
		}
	}
}

